.app_wrapper {
    margin: 0 auto;
}

.content {
    background-color: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0 0 6px -3px;
    padding: 10px;
}


html {
    background-color: #F1F1F1;
}

.bottom_part {
    display: grid;
    width: 1300px;
    margin: 5px auto 0 auto;
    grid-template-columns: 2fr 10fr;
    grid-gap: 0 10px;
}

.app_wrapper_blur {
    filter: blur(2px);
    -webkit-filter: blur(2px);
}